<template>
  <Input
    ref="input"
    v-model="inputValue"
    :type="type"
    :placeholder="placeholder"
    :clearable="clearable"
    :disabled="disabled"
    :size="isMobile ? 'large' : 'default'"
    @on-clear="clearInput"
    class="input-width"
  ></Input>
</template>

<script>
import { Input } from "view-ui-plus";
export default {
  name: "viInput",
  components: { Input },
  props: {
    modelValue: {
      require: true,
    },
    type: {
      type: String,
      default: "text",
    },
    placeholder: {
      type: String,
      default: "请输入",
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isMobile: window.isMobile,
    };
  },
  computed: {
    inputValue: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", val);
        this.$emit("on-change", val);
      }
    },
  },
  methods: {
    clearInput() {
      this.focus();
      this.$emit("on-clear");
    },
    focus() {
      this.$refs.input.focus();
    },
    isFocus() {
      return this.$refs.input.$refs.input === document.activeElement;
    },
  }
};
</script>

<style lang="less" scoped>
.input-width {
  width: 100%;
}
</style>
